#_2KGeP {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

/*  Make our map take the full viewport - 100% */

._3auwv,
._3IFrC {
  width: 100%;
  height: 100%;
}

/* Tooltip code */

._1-CvP {
  font-family: "Baloo Thambi 2", cursive;
  font-size: 10px;
  padding: 0;
  margin: 0;
  color: #424242;
}

._2nBJ5 {
  padding: 1rem;
  margin: 0;
}

._2nBJ5 * {
    margin: 0 0 0.5rem;
    padding: 0;
}

._2nBJ5 p {
  border-bottom: 1px solid rgba(black, 0.2);
}

._2nBJ5 p b {
  font-size: 1.6rem;
  color: #212121;
  padding: 0 5px;
}

._2nBJ5 img {
  width: 4rem;
  height: 4rem;
}

.X3Bvp {
  color: var(--cases-color);
}

._33wTd {
  color: var(--deaths-color);
}

._1_qCQ {
  color: var(--recovered-color);
}

._32Fhn {
  color: var(--active-cases-color);
}

._2wgLF {
  border: 1px solid black;
  position: relative;
  display: block;
  width: 100%;
}

.kEmFC {
  position: absolute;
  z-index: 9;
  color: white;
  right: 0;
  top: 8em;
  cursor: pointer;
  padding-right: .5em;
}